const authServer = 'https://auth.pravah.io:5000/token'
const datalake = 'https://api.datalake.pravah.io:4000/latest'

class Datalake {
    constructor(channel, pravahApiKey) {
        this.channel = channel
        this.key = pravahApiKey
    }

    async latest(count) {

        let body = {
            'authentication_token': this.key
        };

        return new Promise((resolve, reject) => {
            fetch(authServer, {
                method: 'POST',
                body: JSON.stringify(body)
            }).then(response => {
                return response.json()
            }).then(auth => {
                if(auth.error === undefined) {
                    return fetch(datalake, {
                        method: 'POST',
                        body: JSON.stringify({
                            channel: this.channel,
                            access_token: auth.token,
                            'query': {
                                'count': count
                            }
                        })
                    })
                } else {
                    reject(auth)
                }
            }).then(dataResponse => {
                return dataResponse.json()
            }).then(data => {
                resolve(data)
            })
        })

        
    }
}

exports.Datalake = Datalake